import { useEffect } from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';

export default function Hero() {
  useEffect(() => {
    AOS.init({ duration: 1000 });
  }, []);

  return (
    <section data-aos="fade-up" className="relative bg-gradient-to-r from-blue-500 to-indigo-600 text-white py-32 text-center overflow-hidden min-h-[400px] flex items-center justify-center">
      <div className="relative z-10">
        <h1 className="text-4xl md:text-6xl font-extrabold mb-4">Bienvenido a InbiosTech</h1>
        <p className="text-lg md:text-2xl font-medium mb-6">Tu solución moderna y profesional con Next.js</p>
        <a href="#servicios" className="inline-block bg-white text-blue-600 font-semibold px-6 py-3 rounded shadow hover:bg-blue-100 transition">Ver servicios</a>
      </div>
    </section>
  );
}
